.signup-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .signup-section {
    width: 100%;
    max-width: 400px;
  }
  
.address-fetch{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.address-fetch button{
    width: 160px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.location-buttons{
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}
  
  .signup-form {
    display: flex;
    flex-direction: column;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-label {
    font-weight: bold;
  }
  
  .form-input {
    padding: 10px;
    border: 1px solid #dcdcdc;
    border-radius: 6px;
    transition: border-color 0.3s;
    width: 100%;
  }
  
  .form-input:focus {
    border-color: #007bff;
  }
  
  .submit-btn {
    padding: 12px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .submit-btn:hover {
    background-color: #0056b3;
  }
  
  .error-message{
    color: red;
  }