/* footer.css */

.footer {
  background-color: #f0f0f0;
  color: #333;
  padding: 2rem 0;
  /* position: fixed; */
  left: 0;
  bottom: 0;
  width: 100%;
}

.footer-container {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.footer-column {
  flex: 1;
  padding: 0 2rem;
}

.footer-column h3 {
  margin-bottom: 1rem;
}

.footer-column p {
  margin-bottom: 0.5rem;
}

.footer-copyright {
  text-align: center;
}

/* Responsive styles */
@media only screen and (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: center;
  }

  .footer-column {
    flex: none;
    padding: 0;
    text-align: center;
    margin-bottom: 1.5rem; /* Add some space between columns */
  }
  .footer {
    background-color: #f0f0f0;
    color: #333;
    padding: 20px;
    /* position: fixed; */
    left: 0;
    bottom: 0;
    width: 100%;
  }
  
}
