.container {
  margin: 50px;
}

.section {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
}

.item {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
}

.image {
  width: 100%;
  height: auto;
  aspect-ratio: 4/3;
  transition: all ease-in-out 0.3s;
  border-radius: 10px;
}

.image:hover {
  transform: scale(1.1);
}

.text {
  padding: 10px;
}

.swiper {
  padding-top: 20px;
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 500px;
  /* object-fit: contain; */
  border-radius: 20px;
}

/* CouponStyles.css */
.coupon-container {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  color: white;
}

.coupon-title{
  text-align: left;
}
.coupon-content {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 10px;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.coupon-code {
  background-color: rgba(0, 0, 0, 0.5);
  margin-bottom:8px;
  font-size: 18px;
  border: 2px dashed white;
  width: 30%;
}
.coupon-code-copy{
  background: none;
  border: none;
  font-size: 25px;
  color: #fff;
}
.coupon-code-value {
  margin-bottom: 12px;
  font-size: 24px;
}

.coupon-validity {
  margin: 0;
  font-size: 16px;
}


@media only screen and (max-width: 768px) {
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 20px;
  }

  .image {
    width: 100%;
    height: auto;
    /* aspect-ratio: 4/3; */
    transition: all ease-in-out 0.3s;
    border-radius: 10px;
  }
  .container {
    margin: 10px 0px 5px;
  }
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 200px;
    /* object-fit: contain; */
    border-radius: 20px;
  }
}
