/* login.css */

/* Style for the login container */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  /* Style for the login section */
  .login-section {
    background-color: #f4f4f4;
    padding: 20px;
    border-radius: 10px;
  }
  
  /* Style for the login content */
  .login-content {
    /* text-align: center; */
  }
  
  /* Style for the login header */
  .login-header h1 {
    margin-bottom: 10px;
    color: #333;
  }
  
  /* Style for the small text */
  .login-header small {
    color: #666;
  }
  
  /* Style for the form group */
  .form-group {
    margin-bottom: 20px;
  }
  
  /* Style for the form label */
  .form-label {
    display: block;
    margin-bottom: 5px;
    color: #333;
  }
  
  /* Style for the form input */
  .form-input {
    width: 100%;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }
  
  /* Style for the phone input */
  .phone-input {
    position: relative;
  }
  
  /* Style for the submit button */
  .login-form button {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 4px;
    background-color: #1AA79E;
    color: #fff;
    cursor: pointer;
  }
  
  /* Hover effect for the submit button */
  .login-form button:hover {
    background-color: rgb(35, 127, 132);
  }
  
  /* Style for the small forget password text */
  .login-form small {
    display: block;
    margin-top: 10px;
    color: #007bff;
  }
  