/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #1AA79E;
  color: #000;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 10;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.dropdown-content:hover +.dropdown-subcontent,
.dropdown-subcontent:hover {
  display: block;
}
/* Change color of dropdown links on hover */
.dropdown-content a:hover {background-color: #24c2b8;}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {display: block;}

/* Change the background color of the dropdown button when the dropdown content is shown */


/* Subcategory container */
.dropdown-subcontent {
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
  min-width: 160px;
  background-color: #1AA79E;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}

/* Show subcategories on hover */
.dropdown-content a:hover + .dropdown-subcontent {display: block;}

/* Responsive styles */
/* Responsive styles */
@media only screen and (max-width: 768px) {
  .nav-container {
    display: flex;
    align-items: center;
    gap: 5px;
    /* flex-wrap: wrap;  */
  }
  
  .address-container {
    justify-content: flex-start;
    padding: 0 8px;
    flex-grow: 1; /* Allow the address input to take up remaining space */
  }

  .address-input {
    flex-grow: 1; /* Allow the input to take up remaining space */
    max-width: 175px; /* Limit the maximum width of the input */
  }

  .button-container {
    margin-left: 0;
    margin-top: 10px;
  }

  .brobo-img {
    padding-left: 0;
    text-align: center;
  }

  .brobo-img img {
    width: 30px;
  }

  .user-icon {
    font-size: 15px;
    margin-left: 10px;
  }

  .icon-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-right: 20px; /* Restore margin */
  }

  .notification-icon {
    cursor: pointer;
    font-size: 20px;
    margin-right: 10px;
  }

  .cart-icon {
    cursor: pointer;
    font-size: 20px;
  }

  .save-btn, .cancel-btn, .edit-btn {
    padding: 5px 10px;
    margin-left: 5px;
    cursor: pointer;
    border: none;
    border-radius: 4px;
    background-color: #ffff;
    color: #000;
  }

  .offcanvas {
    width: 180px !important;
  }
}

